

export class Question {
    static url = "https://podcast--app-a555e-default-rtdb.europe-west1.firebasedatabase.app/questions.json";



    static async create(question){
        let response;
        try {
            const request = await fetch(Question.url, {
                method: "POST",
                body: JSON.stringify(question),
                headers: {
                    "Content-Type": "application/json"
                }
            });

            response = await request.json();
           




        }
        catch(e){
            throw new e;
        }

        return response;
    }


    static renderList(){
        let html;
        let questions = Question.getQuestionsFromLS();

        if(questions.length){
            html = questions.map((item) => {

                return (
                    `<div class="mui--text-black-54">
                        ${new Date(item.date).toLocaleDateString()}
                        ${new Date(item.date).toLocaleTimeString()}
                    </div>
                    <div class="query">${item.text}</div>
                    <br />`
                )
            }).join("");
        }
        else {
            html = `<div class="mui--text-black-54 resp">Вы ничего не спрашивали</div>`;
        }
        console.log(html)
        return html;
    }


    static async get(url){

        const request = await fetch(url);
        const response = await request.text();


        return response;
    }

    static LS(question){

        const data = Question.getQuestionsFromLS();
        data.push(question);

       localStorage.setItem("questions", JSON.stringify(data))
    }

    static getQuestionsFromLS(){

        return JSON.parse(localStorage.getItem("questions") || "[]")
    }

  
}